(() => {
  const angular = window.angular;

  Controller.$inject = ['$rootScope', 'EquipmentCategory', '$transitions'];

  angular
    .module('app')
    .component('equipmentRankFilter', {
      template:'<div class="form-group"><label>Desde</label> <input type="text" class="form-control" readonly ng-disabled="ctrl.loading" style="background: #fff" enable-time="false" datetime-picker="dd-MM-yyyy" is-open="ctrl.dates.from.open" datepicker-options="ctrl.dates.options" ng-click="ctrl.dates.from.open = !ctrl.dates.from.open" when-closed="ctrl.dates.onClose(args)" ng-model="ctrl.dates.from.value"></div><div class="form-group"><label>Hasta</label> <input type="text" class="form-control" readonly ng-disabled="ctrl.loading" style="background: #fff" enable-time="false" datetime-picker="dd-MM-yyyy" is-open="ctrl.dates.to.open" datepicker-options="ctrl.dates.options" ng-click="ctrl.dates.to.open = !ctrl.dates.to.open" when-closed="ctrl.dates.onClose(args)" ng-model="ctrl.dates.to.value"></div><div class="form-group clearfix"><label>Categorias</label><ui-select ng-model="ctrl.category" theme="select2"><ui-select-match placeholder="Seleccione una categora">{{$select.selected.name}}</ui-select-match><ui-select-choices repeat="category in ctrl.categories | filter: {name: $select.search}"><div ng-bind-html="category.name | highlight: $select.search"></div></ui-select-choices><ui-select-no-choice>No hay elementos disponibles</ui-select-no-choice></ui-select></div><hr><div><button class="btn btn-primary btn-block" ng-click="ctrl.applyFilters()">Filtrar</button></div>',
      controller: Controller,
      controllerAs: 'ctrl'
    });

  function Controller($root, EquipmentCategory, $transitions) {
    const vm = this;

    vm.$onInit = function () {
      vm.applyFilters = applyFilters;

      vm.dates = {
        from: {value: new Date('2018/01/01'), open: false},
        to: {value: new Date('2018/01/01'), open: false},
        options: {showWeeks: false, showMeridian: false},
        onClose: function (args) {
          loadCategories();
        }
      };

      loadCategories()
        .then(() => {
          applyFilters();
        });

      vm.offSuccess = $transitions.onSuccess({}, transition => {
        applyFilters();
      });
    };

    vm.$onDestroy = function () {
      vm.offSuccess();
    };

    function loadCategories() {
      if (vm.prevFrom && vm.prevFrom.getTime() === vm.dates.from.value.getTime() &&
        vm.prevTo && vm.prevTo.getTime() === vm.dates.to.value.getTime()) {
        return;
      }

      vm.loading = true;
      vm.prevFrom = vm.dates.from.value;
      vm.prevTo = vm.dates.to.value;

      return EquipmentCategory
        .find(vm.dates.from.value, vm.dates.to.value)
        .then(categories => {
          vm.categories = categories;
          vm.category = vm.categories.find(current=> current.name === 'Camion');
          vm.loading = false;
        })
        .catch(err => {
          console.log(err);
          vm.loading = false;
        });
    }

    function applyFilters() {
      $root.$emit('filter:update', {
        from: vm.dates.from.value,
        to: vm.dates.to.value,
        category: vm.category
      });
    }
  }
})();