;(() => {
  const angular = window.angular;

  Controller.$inject = ['$rootScope', '$element', 'Equipment'];

  angular
    .module('app')
    .component('equipmentRanking', {
      template:'<div class="panel panel-light"><div class="panel-heading">Estado de flota - Anlisis diario</div><operation-seagull-chart from="ctrl.from" to="ctrl.to" categories="ctrl.categories" is-loading="ctrl.loading"></operation-seagull-chart><div class="centered-message" ng-show="!ctrl.category"><h4 class="no-margin">No se ha seleccionado una categora de equipamiento</h4></div><div ng-show="ctrl.category && !ctrl.loading" class="ng-hide marginLR20 marginT20 paddingB5"><equipment-ranking-details from="ctrl.from" to="ctrl.to" categories="ctrl.categories"></equipment-ranking-details></div></div>',
      controller: Controller,
      controllerAs: 'ctrl'
    });

  function Controller($root, $element, Equipment) {
    const vm = this;

    vm.$onInit = function () {
      const container = $element.find('.chart');
      vm.container = container[0];

      vm.category = null;
      vm.filters = {};
      vm.loading = false;
      vm.offFilters = $root.$on('filter:update', (e, filters) => {
        vm.filters = angular.copy(filters);
        vm.from = vm.filters.from;
        vm.to = vm.filters.to;
        vm.category = vm.filters.category;
        vm.categories = vm.category ? [vm.category] : null;
      });
    };

    vm.$onDestroy = function () {
      vm.offFilters();
    };
  }
})();