(() => {
  const angular = window.angular;

  Controller.$inject = ['$rootScope', '$element', '$timeout', '$compile', '$transitions', '$state'];

  angular
    .module('app')
    .component('moOee', {
      template:'<div class="left-content"><ul class="nav nav-tabs nav-justified nav-tabs secondary"><li ui-sref-active="active"><a ui-sref="mineOperation.oee.overview"><h4>Resumen</h4></a></li><li ui-sref-active="active"><a ui-sref="mineOperation.oee.utilization"><h4>Utilizacin</h4></a></li><li ui-sref-active="active"><a ui-sref="mineOperation.oee.capacityPerformance"><h4>Performance Capacidad</h4></a></li><li ui-sref-active="active"><a ui-sref="mineOperation.oee.timePerformance"><h4>Performance Tiempo</h4></a></li></ul><div class="content"><ui-view></ui-view></div></div><div class="side-panel side-panel-right"><div class="panel panel-filter"><div class="panel-heading">Panel</div><div class="panel-body"><div class="filters-target"></div></div></div></div>',
      controller: Controller,
      controllerAs: 'ctrl'
    });

  function Controller($root, $element, $timeout, $compile, $transitions, $state) {
    const vm = this;

    vm.$onInit = function () {
      vm.filterTarget = $element.find('.filters-target');
      vm.offSuccess = $transitions.onSuccess({}, transition => {
        displayFilters();
      });

      displayFilters();
    };

    vm.$onDestroy = function () {
      vm.offSuccess()
    }

    function displayFilters() {
      const tagName = 'oee-default-filter';
      vm.currentTagName = tagName;

      if (vm.filterScope) {
        vm.filterScope.$destroy();
        vm.filterTarget.empty();
      }

      $timeout(() => {
        vm.filterScope = $root.$new(true);
        vm.filterTarget.append($compile(`<${tagName}></${tagName}>`)(vm.filterScope));
      }, 200);
    }
  }
})();