(() => {
  const angular = window.angular;

  Controller.$inject = ['OEE'];

  angular
    .module('app')
    .component('oeeOverview', {
      template:'<div><oee-overview-chart type="\'OEE\'" data="ctrl.filters.data" from="ctrl.filters.from" to="ctrl.filters.to" categories="ctrl.filters.categories" equipments="ctrl.filters.equipments" chart-title="\'OEE\'" size="\'sm\'"></oee-overview-chart><div class="row"><div class="col-md-4"><oee-overview-chart type="\'Utilization\'" data="ctrl.filters.data" from="ctrl.filters.from" to="ctrl.filters.to" categories="ctrl.filters.categories" equipments="ctrl.filters.equipments" chart-title="\'Utilizacin\'" size="\'xs\'" target="\'mineOperation.oee.utilization\'"></oee-overview-chart></div><div class="col-md-4"><oee-overview-chart type="\'CapacityPerformance\'" data="ctrl.filters.data" from="ctrl.filters.from" to="ctrl.filters.to" categories="ctrl.filters.categories" equipments="ctrl.filters.equipments" chart-title="\'Performance Capacidad\'" size="\'xs\'" target="\'mineOperation.oee.capacityPerformance\'"></oee-overview-chart></div><div class="col-md-4"><oee-overview-chart type="\'TimePerformance\'" data="ctrl.filters.data" from="ctrl.filters.from" to="ctrl.filters.to" categories="ctrl.filters.categories" equipments="ctrl.filters.equipments" chart-title="\'Performance Tiempo\'" size="\'xs\'" target="\'mineOperation.oee.timePerformance\'"></oee-overview-chart></div></div><spinner show="ctrl.filters.loading"></spinner></div>',
      controller: Controller,
      controllerAs: 'ctrl'
    });

  function Controller(OEE) {
    const vm = this;

    vm.$onInit = function () {
      vm.filters = OEE.listenFilters();
    };

    vm.$onDestroy = function () {
      vm.filters.offFilters();
    };
  }
})();