(() => {
  const angular = window.angular;

  Controller.$inject = ['$rootScope', '$timeout', '$element', '$compile', '$transitions', '$state'];

  angular
    .module('app')
    .component('moProduction', {
      template:'<div class="left-content"><ul class="nav nav-tabs nav-justified nav-tabs secondary"><li ui-sref-active="active"><a ui-sref="mineOperation.production.realtime"><h4>Tiempo Real</h4></a></li><li ui-sref-active="active"><a ui-sref="mineOperation.production.ranking"><h4>Anlisis de flota</h4></a></li><li ui-sref-active="active"><a ui-sref="mineOperation.production.compliance"><h4>Cumplimiento</h4></a></li></ul><div class="content"><ui-view></ui-view></div></div><div class="side-panel side-panel-right"><div class="panel panel-filter"><div class="panel-heading">Panel</div><div class="panel-body"><div class="filters-target"></div></div></div></div>',
      controller: Controller,
      controllerAs: 'ctrl'
    });

  function Controller($root, $timeout, $element, $compile, $transitions, $state) {
    const vm = this;

    vm.$onInit = function () {
      vm.filterTarget = $element.find('.filters-target');
      vm.offSuccess = $transitions.onSuccess({}, transition => {
        displayFilters();
      });

      displayFilters();
    };

    vm.$onDestroy = function () {
      vm.offSuccess();
      if (vm.filterScope) {
        vm.filterScope.$destroy();
      }
    };

    function displayFilters() {
      const currentState = $state.current;

      const filtersMap = {
        'mineOperation.production.ranking': 'equipment-rank-filter'
      };

      const tagName = filtersMap[currentState.name] || 'prod-default-filter';
      if (vm.currentTagName === tagName) {
        return;
      }

      vm.currentTagName = tagName;

      if (vm.filterScope) {
        vm.filterScope.$destroy();
        vm.filterTarget.empty();
      }

      $timeout(() => {
        vm.filterScope = $root.$new(true);
        vm.filterTarget.append($compile(`<${tagName}></${tagName}>`)(vm.filterScope));
      }, 200);
    }
  }
})();